<template>
  <div class="page sport_view">
    <template v-if="sportJumpGame == 1">
      <betby />
    </template>
    <template v-if="sportJumpGame == 2">
      <saba />
    </template>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import betby from './betby'
import saba from './saba'
import { getGameInfo } from '@/api/modules/config.js'
import { sport_btr_gid, sport_saba_gid } from '@/api/setting.js'
import { lodash, replaceSrt } from '@/utils/helper.js'
import { getTimeStamp, useTimestamp } from '@/utils/CommonJs'
import store from '@/store'
import { $t } from '@/utils/i18n'
import PublishSubscribe from 'publish-subscribe-js'
import ContainerMixin from '@/components/containerMixin'

export default {
  components: {
    betby,
    saba
  },
  mixins: [ContainerMixin],
  data() {
    return {}
  },
  computed: {
    ...mapState('base', ['isApp', 'systemConfig']),
    // 1:BetBy 2:SaBa
    sportJumpGame() {
      const { sport_jump_game = 1 } = this.systemConfig
      return sport_jump_game
    }
  },
  async beforeRouteEnter(to, from, next) {
    const { systemConfig = {}, user = {}, languageType = '' } = store.state.base
    const { sport_jump_game = 1 } = systemConfig

    // 接口判断是否维护，获取维护信息
    const params = {
      token: user.token,
      language: languageType,
      gid: sport_jump_game == 1 ? sport_btr_gid : sport_saba_gid
    }
    const res = await getGameInfo(params).catch(e => e)
    if (!res || res.code != 0) return next(true)

    const resData = res.data && lodash.isObject(res.data) ? res.data : {}
    // mtc_status:0未维护，1维护中
    const { mtc_status = 0, mtc_st_date = 0, mtc_end_date = 0 } = resData
    // 开始时间是否到达了当前时间
    const beginTime = getTimeStamp(new Date(), mtc_st_date)
    // 当前时间是否到达了结束时间
    const endTime = getTimeStamp(new Date(), mtc_end_date)

    if (mtc_status && beginTime && !endTime) {
      const st_time = useTimestamp(mtc_st_date, 'MM-dd HH:mm') || ''
      const end_time = useTimestamp(mtc_end_date, 'MM-dd HH:mm') || ''

      let content = replaceSrt($t('UI_gameHintTip'), '{st_time}', st_time)
      content = replaceSrt(content, '{end_time}', end_time)

      // 维护弹窗插入序列
      PublishSubscribe.publish(store.state?.dialog?.DUALOG_MANAGER_KEY, {
        key: 'mainten',
        seat: 'first',
        params: {
          title: $t('UI.Tips'),
          content,
          submitText: $t('General.General_1')
        }
      })

      return from.name ? next(false) : next('/menu')
    } else {
      // 标识是否在游戏中
      store.commit('source/SET_IS_TO_GAME', true)
      return next(true)
    }
  }
}
</script>

<style lang="scss" scoped>
.page.sport_view {
  width: 100%;
}
</style>
