<template>
  <div class="comp betby">
    <transition name="select-menu-fade" mode="out-in">
      <div
        v-if="betbyShow"
        class="load_img"
        :style="{
          left: !isApp && leftMenu ? '244px' : 0,
          right: !isApp && rightMenu ? '360px' : 0
        }"
      >
        <img :src="loadIcon('common_loading', false)" />
      </div>
    </transition>
    <div id="betby"></div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import { sport_btr_gid } from '@/api/setting.js'
import PublishSubscribe from 'publish-subscribe-js'
import loadIcon from '@/assets/icons'
import variables from '@style/components/game-container.top-navigation.modules.scss'

export default {
  data() {
    return {
      server: '',
      brand_id: '',
      themeName: '',
      lang: '',
      betbyToken: '',
      betbyShow: true // 体育赛事显隐
    }
  },
  computed: {
    ...mapState('layout', ['containerBox']),
    ...mapState('base', ['user', 'isApp', 'languageType']),
    ...mapState('modal', ['leftMenu', 'rightMenu']),
    ...mapState('source', ['maintains']),
    ...mapGetters('base', ['isTourist']),
    // 头部导航栏高度
    topNavHeight() {
      return Number(variables.top_navigation_height)
    },
    // 推送维护信息
    isMainten() {
      const item = this.maintains.find(f => f.gid == sport_btr_gid) || {}
      return item
    }
  },
  watch: {
    // 监听站内信开关
    rightMenu(bool = false) {
      if (this.isApp || !this.btInstance) return

      this.btInstance.updateOptions({ betSlipOffsetRight: bool ? 360 : 0 })
    },
    // 监听是游客还是用户
    isTourist: {
      async handler() {
        if (this.btInstance) this.btInstance.kill()
        if (this.betTimeout) {
          clearTimeout(this.betTimeout)
          this.betTimeout = null
        }
        this.pageLoaderShow = null
        this.betbyToken = ''

        await this.closeGameDialog().catch(e => e)
        await this.execSlotGame().catch(e => e)

        this.betbyShow = true
        this.persistBTRenderer().then(this.renderView)
      },
      immediate: true
    },
    // 监听是维护信息
    isMainten: {
      handler(item = {}) {
        // mtc_status:0未维护，1维护中
        const { mtc_status = 0, mtc_st_date = 0, mtc_end_date = 0 } = item

        // 开始时间是否到达了当前时间
        const beginTime = this.$u.getTimeStamp(new Date(), mtc_st_date)
        // 当前时间是否到达了结束时间
        const endTime = this.$u.getTimeStamp(new Date(), mtc_end_date)

        // 正在维护
        if (mtc_status && beginTime && !endTime) {
          const st_time = this.$u.useTimestamp(mtc_st_date, 'MM-dd HH:mm') || ''
          const end_time = this.$u.useTimestamp(mtc_end_date, 'MM-dd HH:mm') || ''

          // 维护弹窗插入序列
          PublishSubscribe.publish(this.$store.state?.dialog?.DUALOG_MANAGER_KEY, {
            key: 'mainten',
            seat: 'first',
            params: {
              title: this.$t('UI.Tips'),
              content: this.$t('UI_gameHintTip', { st_time, end_time }),
              submitText: this.$t('General.General_1')
            }
          })

          this.exit()
          this.$router.push({ name: 'menu' })
        }
      },
      deep: true,
      immediate: true
    }
  },
  methods: {
    loadIcon: key => loadIcon(key, false),
    // 打开游戏,上分
    execSlotGame() {
      return new Promise(async (resolve, reject) => {
        const params = {
          token: this.user.token,
          gid: sport_btr_gid,
          language: this.languageType,
          type: this.isApp ? 100 : 101,
          direction: 1
        }

        // 体育赛事金额加载
        this.$store.commit('source/SET_IS_ROTATE', true)
        const resData = await this.$store.dispatch('sport/upperDivision', params).catch(e => e)

        // 体育赛事金额加载完成
        this.$store.commit('source/SET_IS_ROTATE', false)
        if (resData == 'cancal') return reject('cancel')

        const { server = '', brand_id = '', themeName = '', lang = '', token = '' } = resData
        this.server = server
        this.brand_id = brand_id
        this.themeName = themeName
        this.lang = lang
        this.betbyToken = token

        resolve('ok')
      })
    },
    // 退出游戏，下分
    closeGameDialog(callback) {
      return new Promise(async (resolve, reject) => {
        this.$store.commit('source/SET_IS_ROTATE', true) // 大厅的金币刷新开始

        const params = { token: this.user.token, language: this.languageType }
        const res = await this.$store.dispatch('sport/lowerDivision', params).catch(e => e)
        if (res == 'cancel') return reject('cancel')

        resolve('ok')

        // 延迟的作用是以防这时候刚好vip升级，这必须在vip升级弹窗之后, 这个时候洗码量还不一定出来，延迟两秒已经是极限了。
        setTimeout(() => {
          if (callback) callback()
          // 更新红点信息
          Promise.all([
            this.$store.dispatch('base/showRedDotReceivedFun', {
              version: 2,
              event: 'clean_bet',
              language: this.languageType,
              token: this.user.token
            }),
            this.$store.dispatch('base/showRedDotReceivedFun', {
              version: 2,
              event: 'vip_cash_gift',
              language: this.languageType,
              token: this.user.token
            }),
            this.$store.dispatch('base/refreshAccount') // 请求用户信息，金币刷新结束
          ]).then(() => {
            this.$store.commit('source/SET_IS_ROTATE', false)

            // 储存vip保级，vip待领取，洗码的弹窗
            PublishSubscribe.publish(this.$store.state?.dialog?.DUALOG_MANAGER_KEY, { key: 'relegation' })
            PublishSubscribe.publish(this.$store.state?.dialog?.DUALOG_MANAGER_KEY, { key: 'vipReward' })
            PublishSubscribe.publish(this.$store.state?.dialog?.DUALOG_MANAGER_KEY, { key: 'betReward' })
          })
        }, 2 * 1000)
      })
    },
    // 加载体育赛事 sdk
    persistBTRenderer() {
      return new Promise(resolve => {
        if (window.BTRenderer) return resolve()
        const script = document.createElement('script')
        script.src = `${this.server}bt-renderer.min.js`

        script.addEventListener('load', function () {
          resolve()
        })
        document.body.append(script)
      })
    },
    // 初始化体育赛事
    renderView(token = '') {
      const _this = this
      // const BTRenderer = window.BTRenderer
      this.btInstance = new BTRenderer().initialize({
        brand_id: this.brand_id,
        token: token ? token : this.betbyToken,
        themeName: this.themeName,
        lang: this.lang,
        stickyTop: this.topNavHeight || 68,
        target: document.getElementById('betby'),
        betslipZIndex: 99,
        betSlipOffsetTop: 64,
        betSlipOffsetRight: this.isApp ? 0 : this.rightMenu ? 360 : 0,
        betSlipOffsetBottom: this.isApp ? 55 : 0,
        // 路由跳转
        onRouteChange: function () {
          let elEment = document.getElementsByClassName('m_content')[0]
          elEment && elEment.scrollIntoView()
        },
        // 登录
        onLogin: function () {
          _this.$store.dispatch('modal/setLoginRegistShow')
        },
        // 注册
        onRegister: function () {
          _this.$store.dispatch('modal/setLoginRegistShow', { tab: false })
        },
        // token过期
        onTokenExpired: async () => {
          await _this.closeGameDialog().catch(e => e)
          await _this.execSlotGame().catch(e => e)

          return Promise.resolve(_this.betbyToken)
        },
        // 点击更新
        onSessionRefresh: async () => {
          if (!_this.btInstance) return

          _this.btInstance && _this.btInstance.kill()

          await _this.closeGameDialog().catch(e => e)
          await _this.execSlotGame().catch(e => e)

          _this.betbyShow = true
          _this.persistBTRenderer().then(_this.renderView)
        },
        // 打开/关闭投注的回调
        onBetSlipStateChange: function () {},
        // 充值
        onRecharge: () => {
          if (_this.isTourist) {
            _this.$store.dispatch('modal/setLoginRegistShow')
          } else {
            _this.$store.dispatch('modal/setDepositWithdrawShow')
          }
        }
      })

      // 监听体育赛事 loading 消失
      this.betTimeoutFun()
    },
    // 监听体育赛事 loading 消失
    betTimeoutFun() {
      if (this.betTimeout) {
        clearTimeout(this.betTimeout)
        this.betTimeout = null
      }
      if (!this.betbyShow) return

      const pageLoader = document
        .getElementById('bt-inner-page')
        ?.shadowRoot?.querySelector("div[data-editor-id='pageLoader']")
      const currentLoad = !!pageLoader

      this.betTimeout = setTimeout(() => {
        if (this.pageLoaderShow && !currentLoad) {
          this.betbyShow = false
        } else {
          // 记录当前的loading状态
          this.pageLoaderShow = currentLoad
          this.betTimeoutFun()
        }
      }, 1 * 1000)
    },
    // 退出操作
    exit() {
      this.btInstance && this.btInstance.kill()
      this.closeGameDialog(() => {
        // 提现提醒弹窗插入序列
        PublishSubscribe.publish(this.$store.state?.dialog?.DUALOG_MANAGER_KEY, { key: 'remind', seat: 'first' })
      })

      if (this.betTimeout) {
        clearTimeout(this.betTimeout)
        this.betTimeout = null
      }
      this.pageLoaderShow = null
    }
  },
  beforeDestroy() {
    // 标识是否在游戏中
    this.$store.commit('source/SET_IS_TO_GAME', false)
    this.exit()
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/dialog.modules.scss';

// 针对移动端样式
.comp.betby {
  width: 100%;
  min-height: calc(100vh - 120px);
  padding: 0;
  .load_img {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 108;
    background: $project_bg;
    img {
      width: $game_load_img_width;
      position: absolute;
      left: $game_load_img_left;
      top: $game_load_img_top;
      transform: translate(-50%, -50%);
      z-index: 1;
    }
  }
}

// 针对pc端样式
@include breakpoint-only-up() {
  .comp.betby {
    min-height: calc(100vh - 64px);
  }
}

.select-menu-fade-enter-active {
  transition: all 0.4s ease;
}

.select-menu-fade-leave-active {
  transition: all 0.4s ease;
  transform: translateY(-100%);
}

.select-menu-fade-enter,
.select-menu-fade-leave-to {
  transform: translateY(-100%);
}
</style>
